body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #71008d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
    display: flex;
    flex-wrap: wrap;
    width: 90%;
    height: auto;
    margin: auto;
    align-items: center;
}

.logo-container, .nav-links {
    display: flex;
}

.logo {
    font-weight: 400;
}

.logo-container img {
  width: 200px;
}

.logo-container {
    flex: 1;
    margin: 20px;
}

nav {
    flex: 2;
}

.nav-links {
    justify-content: space-around;
    list-style: none;
}

.nav-link {
    color: #ffffff;
    font-size: 18px;
    font-weight: 900;
    text-decoration: none;
    transition: 0.3s;
}

.nav-link:hover {
  color: #f76692;
}



@media only screen and (max-width: 475px) {
  .logo-container img {
    width: 100%;
    min-width: 100px;
  }
  .nav-link {
    font-size: 12px;
  }
}