.recipe {
    border-radius: 10px;
    margin: 20px;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: #ffffff;
    align-items: center;
    min-width: 40%;
}

.image {
    border-radius: 50%;
    width: 60%;
    max-width: 350px;
    margin-top: 25px;
}

h1 {
    font-family: 'Cookie', cursive;
    font-weight: 400;
    margin: 20px;
    text-align: center;
}

ol li {
    margin-right: 20px;
}

p span {
    font-weight: bold;
}